import { createStore } from 'vuex'
import Web3 from 'web3';
import Web3Util from '@/utils/Web3Util';

export default createStore({
  state: {
    currnetChainId:'56',
    chainId:'',
    display_wallet_address:null,
    wallet_address:null,
    web3:null,
    provider:null,
    contract:null,
    isMobile: false,
    wallet_type:"",
    showLink:false
  },
  getters: {},
  mutations: {
    updateWallet(state, val) {
      state.chainId = val.chainId;
      state.provider = val.provider;
      state.web3 = new Web3(val.provider);
      state.wallet_type = val.type;
      state.wallet_address = val.address;
      let addr = val.address;
      if (addr) {
        let str_a = addr.substr(0, 5);
        let str_b = addr.substr(addr.length - 5);
        addr = str_a + "..." + str_b;
      }
      state.display_wallet_address = addr;
    },
    setIsMobile(state, val) {
      state.isMobile = val
    },
    setShowLink(state, val) {
      state.showLink = val
    },
    setCurrnetChainId(state, val) {
      state.currnetChainId = val.chainId
    },
  },
  actions: {
    async switchChainid({commit},chainId){
      commit('setCurrnetChainId',{chainId});
    },
    async connectWallet({commit},chainId) {
      try{
        if(chainId=="828"){
          if(!window.aleereum){
            window.open("https://www.alewallet.app/")
            return;
          }
          let provider = window.aleereum;
          provider.on('on_account_change', async function(accounts) {
            if(provider.networkId!=3){
              provider.switchNetwork(3)
            }else{
              commit('updateWallet',{provider:provider,address:provider.account,type:"Ale",chainId:chainId});
            }
          })
          provider.on('on_networkId_change', async function(network) {
            if(provider.networkId!=3){
              await provider.switchNetwork(3)
            }
          })
          if (!provider.isConnected || provider.islocked){
            await provider.connect()
          }
          if(provider.networkId!=3){
            await provider.switchNetwork(3)
          }else{
            commit('updateWallet',{provider:provider,address:provider.account,type:"Ale",chainId:chainId});
          }
        }else{
          if(!window.ethereum){
            window.open("https://metamask.io")
            return;
          }
          const v = this;
          const provider = window.ethereum;
          const net = Web3Util.getNetwork(chainId);
          const cid = await new Web3(provider).eth.net.getId()+"";
          provider.on('accountsChanged', function (accounts) {
            if(accounts!=v.state.wallet_address){
              location.reload()
            }
          });
          provider.on('networkChanged', async function(cid) {
            if(cid==chainId){
              await provider.enable();
              let accounts = await provider.request({method:'eth_requestAccounts'});
              commit('updateWallet',{provider:provider,address:accounts[0],type:"MetaMask",chainId:chainId});
            }
          });
          if(cid!=chainId){
            await provider.request({method:'wallet_addEthereumChain',params:[net]}).catch(error=>{});
          }else{
            await provider.enable();
            let accounts = await provider.request({method:'eth_requestAccounts'});
            commit('updateWallet',{provider:provider,address:accounts[0],type:"MetaMask",chainId:chainId});
          }
        }
      }catch(e){
        alert(e)
        console.log(e);window.notification["error"]({message:"Error",description:"Connect wallet error"});
      }
    },
    showWalletModal({ commit }, val) {
      commit('setShowLink',val)
    },
    setismobile({ commit }, val) {
      commit('setIsMobile', val)
    },
  },
  modules: {

  }
})
