import Big from 'big.js';
import Bignumber from 'bignumber.js';
import dayjs from 'dayjs';

class MathUtil {

    stringToHex(str){
        var val = "";
        for (var i = 0; i < str.length; i++) {
            if (val == "")
                val = str.charCodeAt(i).toString(16);
            else
                val += str.charCodeAt(i).toString(16);
        }
        val = "0x"+val
        return val
    }

    minus(a,b,fix){
        try{
            return Big(a).minus(b).toFixed(fix,Big.roundDown)
        }catch(e){
            return ""
        }
    }

    minusPow(a,pow,fix){
        try{
            return Big(a).minus(Big(10).pow(pow)).toFixed(fix,Big.roundDown)
        }catch(e){
            return ""
        }
    }

    times(a,b,fix){
        try{
            return Big(a).times(b).toFixed(fix,Big.roundDown)
        }catch(e){
            return ""
        }
    }

    timesPow(a,pow,fix){
        try{
            return Big(a).times(Big(10).pow(pow)).toFixed(fix,Big.roundDown)
        }catch(e){
            return ""
        }
    }

    div(a,b,fix){
        try{
            return Big(a).div(b).toFixed(fix,Big.roundDown)
        }catch(e){
            return ""
        }
    }

    divPow(a,pow,fix){
        try{
            return Big(a).div( Big(10).pow(pow) ).toFixed(fix,Big.roundDown)
        }catch(e){
            return ""
        }
    }

    numberToHex(a){
        try{
            return Bignumber(a).toString(16)
        }catch(e){
            return ""
        }
    }

    gt(a,b){
        try{
            return Big(a).gt(Big(b))
        }catch(e){
            return ""
        }
    }

    gte(a,b){
        try{
            return Big(a).gte(Big(b))
        }catch(e){
            return ""
        }
    }

    formatter(date){
       return dayjs(date).format('YYYY-MM-DD HH:mm:ss');
    }
}

export default new MathUtil();
