<template>
  <a-modal
      centered
      width="420px"
      v-model:open="showLink"
      title="Connect to a wallet"
      :footer="null"
      class="link_modal_div"
      @cancel="handleCancel"
  >
    <div class="modal_content">
       <div class="modal_item flex_row_space_between" v-for="(item, index) in list" :key="index" @click="saveLinkWallet(item)">
         <p>{{item.name}}</p>
         <img :src="item.icon" alt=""/>
       </div>
    </div>
  </a-modal>
</template>

<script>
import {computed, defineComponent, reactive, toRefs} from "vue";
import {useStore} from "vuex";

export default defineComponent({
  name: "WalletModal",
  setup() {
    const store = useStore();
    const showLink = computed({
      get() {
        if(store.state.currnetChainId=="828"){
          Data.list = [{name: "AleWallet", icon: require('@/assets/img/ale.png')}]
        }else{
          Data.list = [{name: "MetaMask", icon: require('@/assets/img/metamask.png')}]
        }
        return store.state.showLink
      },
      set(newValue) {
        store.dispatch('showWalletModal', newValue)
      }
    });
    const Data = reactive({
       list: []
    });
    const Methods = {
      handleCancel() {
        store.dispatch('showWalletModal',false);
      },
      saveLinkWallet(item) {
        store.dispatch('showWalletModal',false);
        store.dispatch('connectWallet',store.state.currnetChainId)
      }
    };

    return {
      showLink,
      ...toRefs(Data),
      ...Methods,
    }
  }
})
</script>

