<template>
  <a-config-provider :theme="themes">
    <div class="app_bg"></div>
    <Header/>
    <router-view/>
    <WalletModal/>
  </a-config-provider>
</template>
<script>
import Header from '@/components/Header.vue';
import WalletModal from "@/components/WalletModal";
import {notification} from 'ant-design-vue';
import {onMounted, reactive, toRefs, watch} from "vue";
import {useStore} from "vuex";
export default {
  name: 'App',
  components: {Header,WalletModal},
  setup() {
    const store = useStore();

    const Data = reactive({
      screenWidth: null,
      themes: {
        token: {
          "colorPrimary": "rgb(0, 88, 255)",
        },
      }
    })

    watch(() => Data.screenWidth,(val) => {
      if (val < 767) {
        store.dispatch('setismobile', true);
      } else {
        store.dispatch('setismobile', false);
      }
    })

    onMounted(() => {
      window.notification = notification;
      Data.screenWidth = document.body.clientWidth
      window.onresize = () => { 
        return (() => {
          Data.screenWidth = document.body.clientWidth
        })()
      }
    })

    return {
      ...toRefs(Data),
    }
  }
}

</script>
<style lang="less">
.app_bg {
  position: absolute;
  top: 86px;
  left: 0px;
  right: 0px;
  width: 100%;
  height: 738px;
  background-repeat: no-repeat;
  background-image: url(./assets/img/swap_bg.png);
  filter: brightness(104%) hue-rotate(0deg) saturate(102%) contrast(100%);
  background-position: 50% 50%;
  background-size: cover;
}
</style>
