         </div>
       </a>
      <div class="tab_box">
        <a href="https://oortswap.com/">Home</a>
        <a href="https://app.oortswap.com/#/swap">Swap</a>
        <a href="https://app.oortswap.com/#/pool">Pools</a>
        <router-link class="menu_box" active-class="active_menu" to="/">Bridge</router-link>
        <a href="https://huygens.ccnswap.org/#/swap">Ccnswap</a>
      </div>
    </div>
    <div class="head_right">
      <a-button class="head_right_btn" :size="isMobile?'middle':'large'" @click="login">
        <img v-show="walletType!=''" :src="walletType=='MetaMask'?require('@/assets/img/metamask.png'): require('@/assets/img/ale.png')" alt=""/>
        {{ (address && address.length > 0) ? address : 'Connect to a Wallet' }}
      </a-button>
    </div>
  </div>
</template>

<script>
import {useStore} from "vuex"
import {computed,reactive,toRefs} from 'vue';

export default ({
  name: "Header",
  setup() {
    const store = useStore();
    const Data = reactive({
      address: computed(() => {
        return store.state.display_wallet_address
      }),
      walletType: computed(() => {
        return store.state.wallet_type
      }),
      isMobile: computed(() => {
        return store.state.isMobile
      }),
    })
    const Methods = {
      login() { 
        store.dispatch('showWalletModal',true);
      }
    }
    return { ...toRefs(Data), ...Methods}
  }
})
</script>

